.backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0,0.3);
    padding: 10%
}

.modal {
    background-color: #fff;
    border-radius: 5px;
    max-width: 500px;
    margin: 0 auto;
    padding: 30px
}

.modalText {
    min-height: 100px;
    padding-top: 5%
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.titles {
  text-align: center;
  padding: 2%
}

.question {
  border: 1px solid #2196F3;
  border-bottom: none;
  padding: 2%;
}

.question:nth-child(5) {
  border-bottom: 1px solid #2196F3;
}

/*buttons*/

.btn {
  border: none; /* Remove borders */
  color: white; /* Add a text color */
  padding: 14px 28px; /* Add some padding */
  cursor: pointer; /* Add a pointer cursor on mouse-over */
  background-color: #2196F3;
  display: table;
  margin: 2% auto;
}

.btn:hover {background: #0b7dda;}

.btn-inline {
  margin-right: 9%;
  display: inline-block;
  width: 20%;
}

.btn-inline:last-child {margin-right: 0;}

.btn-footer {
  text-align: center;
}

.question-body {
  padding-top: 2%;
}

.container, .container-checkbox {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

input[type="text"] {
  width: 20%;
  padding: 8px 10px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  resize: none
}

.question-title {
  padding-bottom: 0.5rem;
}

.result-text {
    padding: 5% 0 3% 42%;
}

.result-text h3 {
    font-size: 1.5rem;
}

.result-item {
    padding-top: 3%;
    font-size: 1.2rem;
}
/*Reset*/
html, body, div, section, article, p, blockquote, ul, li, span{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Calibri', sans-serif;
}

a {
    text-decoration: none;
    cursor: pointer;
}

img {
    border: 0;
}

button {
    cursor: pointer;
    outline: none;
}

h1, h2, h3, h4, h5, p {
    color: #000000;
    margin: 0;
}

h1 {
    font-weight: 700;
    line-height: 1.08;
}

h2 {
    font-weight: 700;
    line-height: 1.08;
}

ul {
    list-style-type: none;
}

/* Hide the browser's default radio button */
.container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
}

/* Create a custom radio button */
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
    border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.container:hover input ~ .checkmark {
    background-color: #ccc;
}

/* When the radio button is checked, add a blue background */
.container input:checked ~ .checkmark {
    background-color: #2196F3;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the indicator (dot/circle) when checked */
.container input:checked ~ .checkmark:after {
    display: block;
}

/* Style the indicator (dot/circle) */
.container .checkmark:after {
    top: 9px;
    left: 9px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: white;
}
/* Hide the browser's default checkbox */
.container-checkbox input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.checkmark-checkbox {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
}

/* On mouse-over, add a grey background color */
.container-checkbox:hover input ~ .checkmark-checkbox {
    background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.container-checkbox input:checked ~ .checkmark-checkbox {
    background-color: #2196F3;
}

/* Create the checkmark-checkbox/indicator (hidden when not checked) */
.checkmark-checkbox:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark-checkbox when checked */
.container-checkbox input:checked ~ .checkmark-checkbox:after {
    display: block;
}

/* Style the checkmark-checkbox/indicator */
.container-checkbox .checkmark-checkbox:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
.select-style {
    border: 1px solid #ccc;
    width: 20%;
    border-radius: 3px;
    overflow: hidden;
    background: url(data:image/png;base64,) no-repeat 95% 50%;
    background-size: 5%;
}

.select-style select {
    padding: 5px 8px;
    width: 100%;
    border: none;
    box-shadow: none;
    background: transparent;
    background-image: none;
    -webkit-appearance: none;
}

.select-style select:focus {
    outline: none;
}
